<template>
  <a-modal
    :visible="visible"
    :title="category ? '' : ''"
    :okText="category ? '' : ''"
    cancelText=""
    @cancel="$emit('cancel')"
    @ok="onSubmit"
  >
    <a-form-model layout="vertical" ref="form" :model="form" :rules="rules">
      <a-form-model-item ref="name" label="" prop="name">
        <a-input v-model="form.name" @pressEnter="onSubmit" />
      </a-form-model-item>

      <a-form-model-item ref="color" label="" prop="color">
        <a-input v-model="form.color" @pressEnter="onSubmit">
          <a-dropdown slot="suffix" placement="bottomRight">
            <div
              class="color__block"
              :style="
                `width:2rem;height:2rem;background-color:${form.color};border-radius: 10rem;`
              "
            ></div>
            <chrome-picker
              @input="selectColor"
              v-model="form.color"
              slot="overlay"
              :palette="palette"
            ></chrome-picker>
          </a-dropdown>
          <a-dropdown slot="prefix" placement="bottomLeft">
            <div
              class="color__block"
              :style="
                `width:1.5rem;height:1.5rem;background-color:${form.color};border-radius: 10rem;`
              "
            ></div>
            <compact-picker
              @input="selectColor"
              v-model="form.color"
              slot="overlay"
              :palette="palette"
              style="width: 8.5rem;"
            ></compact-picker>
          </a-dropdown>
        </a-input>
      </a-form-model-item>
      <a-form-model-item ref="description" label="" prop="description">
        <a-input v-model="form.description" @pressEnter="onSubmit" />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
</template>

<script>
//TODO 
import { Compact, Chrome } from "vue-color";
import { mapActions, mapGetters } from "vuex";

export default {
  props: ["visible", "category"],
  components: {
    "compact-picker": Compact,
    "chrome-picker": Chrome
  },
  data() {
    return {
      palette: [
        "#f14ba9",
        "#f76964",
        "#ff8800",
        "#ffc60a",
        "#34c724",
        "#00d6b9",
        "#4e83fd",
        "#14c0ff",
        "#616ae5",
        "#935af6",
        "#d136d1",
        "#8f959e"
      ],
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              for (let id in this.categorys) {
                if (!this.category && this.categorys[id].name === value)
                  callback("");
              }
              callback();
            },
            trigger: "blur"
          }
        ],
        color: [
          {
            required: true,
            message: "",
            trigger: "change"
          },
          {
            pattern: /^#([a-fA-F\d]{6}|[a-fA-F\d]{3})$/,
            message: "HEX:#FFC60A",
            trigger: "change"
          }
        ]
      },
      form: {
        color: "#FFC60A",
        name: "",
        description: ""
      }
    };
  },
  computed: {
    ...mapGetters(["categorys"])
  },
  mounted() {
    if (this.category) {
      this.form.color = this.category.color;
      this.form.name = this.category.name;
      this.form.description = this.category.description;
    }
  },
  methods: {
    selectColor(value) {
      this.form.color = value.hex;
    },
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.category) {
            this.updatedCategory({ id: this.category.id, ...this.form });
            this.$message.success("");
          } else {
            this.createCategory(this.form);
            this.$message.success("");
          }

          this.$emit("create");
          this.$refs.form.resetFields();
        } else {
          this.$message.error("");

          return false;
        }
      });
    },
    getCategorys() {
      console.log("34");
    },
    ...mapActions(["createCategory", "updatedCategory"])
  }
};
</script>

<style>
.vc-compact-color-item {
  border-radius: 10rem;
}
</style>
