<template>
  <a-card hoverable>
    <createCategory
      ref="collectionForm"
      v-if="visible"
      :visible="visible"
      @cancel="visible = false"
      @create="visible = false"
      :category="category"
    />
    <shortcutView
      v-if="showShortcut"
      @ok="showShortcut = false"
      :category="category"
    />
    <template slot="actions" class="ant-card-actions">
      <a-icon
        key="star"
        type="star"
        v-if="category.active"
        :theme="star ? 'filled' : 'outlined'"
        @click="handleStar"
      />
      <a-icon key="edit" type="edit" @click="visible = true" />
      <a-dropdown placement="bottomLeft">
        <a-icon key="ellipsis" type="ellipsis" />
        <a-menu slot="overlay">
          <a-menu-item @click="showShortcut = true" v-if="category.active">
            <a-icon key="radius-bottomleft" type="radius-bottomleft" />
            <span></span>
          </a-menu-item>
          <a-menu-item @click="handleArchive">
            <a-icon
              key="folder-open"
              type="folder-open"
              v-if="category.active"
            />
            <a-icon v-else type="undo" />
            <span v-if="category.active"></span>
            <span v-else></span>
          </a-menu-item>
          <a-menu-item @click="handleDel">
            <a-icon key="delete" type="delete" />
            <span></span>
          </a-menu-item>
        </a-menu>
      </a-dropdown>
    </template>
    <a-card-meta :title="category.name" :description="category.description">
      <a-avatar slot="avatar" :style="`backgroundColor: ${category.color}`" />
      <!-- <span class="event-color" slot="avatar"></span> -->
    </a-card-meta>
  </a-card>
</template>

<script>
import createCategory from "./CategoryForm";
import shortcutView from "./CategoryShortcut";
import { mapActions } from "vuex";

export default {
  // active: (...)
  // color: (...)
  // description: (...)
  // id: (...)
  // name: (...)
  // shortcut: (...)
  // star: (...)
  props: ["category"],
  components: {
    createCategory,
    shortcutView
  },
  data() {
    return {
      visible: false,
      showShortcut: false
    };
  },
  computed: {
    archive() {
      return this.$store.getters.categorys[this.category.id].active;
    },
    star() {
      return this.$store.getters.categorys[this.category.id].star;
    }
  },
  methods: {
    async handleStar() {
      this.toggleStar(this.category.id);
    },
    handleDel() {
      this.$modal.confirm({
        title: "",
        content: "",
        okText: "",
        okType: "danger",
        cancelText: "",
        onOk: () => {
          this.deleteCategory(this.category.id);
        }
      });
    },
    handleArchive() {
      this.toggleArchive(this.category.id);
      if (this.category.active) {
        this.$message.success("");
      } else {
        this.$message.success("");
      }
    },
    ...mapActions(["toggleStar", "deleteCategory", "toggleArchive"])
  }
};
</script>

<style>
.ant-card-meta-description {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>
