<template>
  <div style="background-color: #F5F5F5;">
    <createCategory
      ref="categoryForm"
      :visible="visible"
      v-if="visible"
      @cancel="hideForm"
      @create="hideForm"
    />
    <a-page-header
      :ghost="false"
      title=""
      sub-title=""
      :backIcon="false"
    >
      <template slot="extra">
        <a-button type="dashed" icon="plus" @click="showForm"
          ></a-button
        >
      </template>
      <template slot="footer">
        <a-tabs default-active-key="active" @change="changeTab">
          <a-tab-pane key="active" tab="" />
          <a-tab-pane key="archive" tab="" />
        </a-tabs>
      </template>
    </a-page-header>
  </div>
</template>
<style></style>

<script>
// import createCategoryCard from "../../components/createCategoryCard"
import createCategory from "./CategoryForm";
import { mapActions } from "vuex";

export default {
  components: { createCategory },

  data() {
    return {
      visible: false
    };
  },
  methods: {
    changeTab(activeKey) {
      this.$emit("change", activeKey);
    },
    showForm(event) {
      if (event.screenX + event.screenY + event.clientX + event.clientY === 0)
        return; //click,
      this.visible = true;
    },
    hideForm() {
      this.visible = false;
    },
    ...mapActions(["createCategory"])
  }
};
</script>
