<template>
  <a-modal
    :title="`[${this.category.name}]`"
    :visible="true"
    okText=""
    cancelText=""
    width="590px"
    @cancel="
      () => {
        $emit('ok');
      }
    "
    @ok="
      () => {
        $emit('ok');
      }
    "
  >
    <a-transfer
      :data-source="eventList"
      show-search
      :locale="{
        itemUnit: '',
        itemsUnit: '',
        notFoundContent: '',
        searchPlaceholder: ''
      }"
      :list-style="{
        width: '250px',
        height: '300px'
      }"
      :filter-option="filterOption"
      :target-keys="targetKeys"
      :render="item => `${item.title} - [${item.abbr}]`"
      @change="handleChange"
      @search="handleSearch"
    ></a-transfer>
  </a-modal>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["event", "category"],
  data() {
    return {
      targetKeys: [],
      eventList: []
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      //
      const targetKeys = [];
      const eventList = [];

      //
      const category = this.$store.getters.categorys[this.category.id];
      const events = this.$store.getters.events;

      for (const key in events) {
        const data = {
          key: events[key].id,
          abbr: events[key].abbr,
          title: events[key].name,
          description: events[key].description
          //filterOption
        };
        //
        if (category.shortcut.includes(key)) {
          targetKeys.push(key);
        }

        eventList.push(data);
      }

      this.eventList = eventList;
      this.targetKeys = targetKeys;
    },
    filterOption(inputValue, item) {
      const serach = inputValue.toUpperCase();
      return (
        item.abbr.toUpperCase().match(serach) ||
        item.title.toUpperCase().match(serach) ||
        item.description.toUpperCase().match(serach)
      );
    },
    // handleChange(targetKeys, direction, moveKeys) {
    handleChange(targetKeys) {
      this.setShortcut({ id: this.category.id, shortcut: targetKeys });
      this.targetKeys = targetKeys;
    },
    // handleSearch(dir, value)
    handleSearch() {
      // console.log(":", dir, value);
    },
    ...mapActions(["setShortcut"])
  }
};
</script>
